/*
 * Media Queries
 * --------------------------------------------------
 *
 * Use as: @include respond-to(small) { ... }
 *    @include respond-to(medium) { ... }
 *    @include respond-to(large) { ... }
 */

@mixin respond-to($breakpoint) {
  // Extra small devices (phones, 544px and up)
  @if $breakpoint == "xsmall" {
    @media (min-width: 34em) {
      @content;
    }
  }

  // Small devices (landscape phones, 768px and up)
  @else if $breakpoint == "small" {
    @media (min-width: 48em) {
      @content;
    }
  }
  // Medium devices (crappy laptops, tablets, 992px and up)
  @else if $breakpoint == "medium" {
    @media (min-width: 62em) {
      @content;
    }
  }
  // Large devices (desktops, 1200px and up)
  @else if $breakpoint == "large" {
    @media (min-width: 75em) {
      @content;
    }
  }

  //Extra large devices (large desktops, 1900px and up)
  @else if $breakpoint == "xlarge" {
    @media (min-width: 118em) {
      @content;
    }
  }
  
  // Short-cut to target extra small devices *only* (phones, 544px and up)
  @else if $breakpoint == "smallonly" {
    @media (max-width: 34em) {
      @content;
    }
  }
}


@media print {
    @page {
        size: 330mm 427mm;
        margin: 1cm;
    }
    a[href]:after {
        content: none !important;
    }
    .container {
        width: 1170px;
    }
    .navbar, #footer {
        display: none;
    }
    body {
        overflow: visible !important; 
    }
    div.container, div.container-fluid {
        margin: none;
        padding: none;
    }
    div.row, div.row-fluid {
        margin: none;
        padding: none;
    }
    h2, h3, h4, h5, h6 {
       page-break-after: avoid;
    }
    /*img {
       max-width: 100% !important;
    }*/
    ul, img {
       page-break-inside: avoid;
    }
}

/*
 * Colors
 * --------------------------------------------------
 */

$main: #800000;
$accent: #670000;
$mid-dark: #525252;
$darkgray: #767676;


/*
 * Global
 * --------------------------------------------------
 */

body {
  padding-top: 50px;
  font-family: 'ProximaNova-Regular', "Arial", sans-serif;
  @include respond-to(small) {
  padding-top: 100px;
  }
}


/*
 * Main Navbar
 * --------------------------------------------------
 */

.fluid-top {
  @include respond-to(small) {
    padding: 0px;
  }
}

.navbar-brand {
  max-width: 250px;
  padding: 5px 15px 25px 15px;
  @include respond-to(small) {
    max-width: 275px;
    padding-top: 15px;
  }
  @include respond-to(medium) {
    max-width: 350px;
    padding: 7px 15px 25px 35px;
  }
}

.navbar>.container-fluid .navbar-brand {
  @include respond-to(medium) {
    margin-left: 15px;
  }
}

.navbar-default {
  border: 0px;
  background-repeat: repeat;
  background-color: $main;
  z-index: 10;
  border-radius: 0;
}

#navbar {
  background-color: $accent;
  font-size: 1.1em;
  padding-left:0.5em;
  @include respond-to(small) {
    margin-top: 60px;
  }
  .navbar-nav {
    float: left;
    @include respond-to(small) {
      float: right;
    }
    &>li>a {
      color: #eee;
      padding: 10px;
      &:hover {
        color: #fff;
        background-color: #642822;
      }
      &:focus {
        color: #fff;
      }
    }
  }
}


/*
 * Off Canvas (Sidebar Mobile Toggle)
 * --------------------------------------------------
 */

@media screen and (max-width: 986px) { // Keep as @media (instead of @include) for stability
 .row-offcanvas {
    position: relative;
    -webkit-transition: all .25s ease-out;
         -o-transition: all .25s ease-out;
            transition: all .25s ease-out;
  }

  .row-offcanvas-right {
    right: 0;
      .sidebar-offcanvas {
        right: -50%; /* 6 columns */
    }
    &.active {
        right: 50%; /* 6 columns */
    }
  }

  .row-offcanvas-left {
    left: 0;
    min-height: 900px; // fix to sidebar overlapping footer in mobile.
      .sidebar-offcanvas {
        left: -50%; /* 6 columns */
    }
    &.active {
        left: 50%; /* 6 columns */
    }
  }

  .sidebar-offcanvas {
    position: absolute;
    top: 0;
    width: 50%; /* 6 columns */
  }

  .toggle-side {
      padding-bottom: 5px;
  }
}


/*
 * Left Sidebar
 * --------------------------------------------------
 */

.sidebar {
  background-color: rgba(210,205,204,0.6);
  padding: 1em 1em 1em 2em;
  @include respond-to(medium) {
    padding-left: 1em;
    padding-top: 35px;
  }
  >ul {
    list-style-type: none;
    padding-left: 1em;
    text-indent: -1em;
    >li { // Direct child page
      margin: 1em 0;
      font-weight: 600;
      a {
        color: $main;
      }
      & ul { // Grandchild page
        list-style-type: none;
        padding-left: 1em;
        text-indent: -1em;
        li {
          font-weight: 200;
          margin-top: 0.5em;
          &:first-child {
            margin-top: 0.2em;
          }
          a {
            color: $mid-dark;
          }
        }
      }
    }
  }
  h2, h2 a {
    margin-bottom: 3px;
    font-size: 1em;
    font-weight: 600;
    color: $accent;
    font-style: italic;
  } 
} // END Sidebar nesting

.btn-sidebar {  // Toggle button for mobile view
  background: #ddd;
  color: $darkgray;
  position: absolute;
  left: 0;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  margin-left: 0;
  margin-top: -2em;
  z-index: 1000;
  background-color: rgba(210,205,204, 0.9);
  box-shadow: 0px 2px 7px $darkgray;
  & a {
    color: $darkgray;
  }
}

/*
 * Footer
 * --------------------------------------------------
 */

.footer {
  width: 100%;
  background: $main;
  background-repeat: repeat;
  padding-top: 20px;
  padding-bottom: 20px;
  position: absolute;
  bottom: 0;
}


